import React, { useState } from 'react'
import { useFirebase } from "gatsby-plugin-firebase"

import { Link } from 'gatsby'
import moment from 'moment'

import classNames from 'classnames'
import styles from './Blog.module.scss'

const Blog = ({ blogData }) => {
  const [viewCount, updateViewCount] = useState(null)

  useFirebase(firebase => {
    firebase
      .database()
      .ref(`${blogData.id}/viewCount`)
      .once('value')
      .then(snapshot => {
        if (snapshot.val()) {
          updateViewCount(snapshot.val())
        } else {
          updateViewCount(0)
        }
      })
  }, [])

  return (
    <Link className={styles.link} to={blogData.path}>
    <div className={styles.blog} key={blogData.path}>
      <div className={styles.top}>
        <span className={styles.postedAt}>{moment(blogData.date).format('LL')}</span>
        <span className={styles.timeToRead}>{blogData.timeToRead} min read</span>
      </div>
      <h2 className={styles.postTitle}>{blogData.title}</h2>
      <div className={styles.flex}>
      {
        blogData.tags && blogData.tags.length ? (
          <div className={styles.tags}>
            {
              blogData.tags.map(tag => (
                <Link className={classNames([styles.link, styles.tag])} key={tag} to={`/tags/${tag}`}>
                  #{tag}
                </Link>
              ))
            }
          </div>
        ) : null
      }
      {
        viewCount !== null ? (
          <span className={styles.viewCount}>({viewCount} view{viewCount > 1 ? 's' : null})</span>
        ) : null
      }
      </div>
    </div>
    </Link>
  )
}

export default Blog